import React, { useState, useEffect } from 'react';

// reactstrap components
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  CardTitle,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col,
} from "reactstrap";

// core components
import Navbar from "components/Navbars/Navbar.js";
import Header from "components/Headers/Header.js";
import Footer from "components/Footers/Footer.js";

import articleContent from './Article-Content';
import UpvoteSection from '../components/Article/UpvoteSection';
import AddCommentForm from '../components/Article/AddCommentForm';
import CommentsList from '../components/Article/CommentsList';
import ArticlesList from '../components/Article/ArticlesList';
import Error404 from './Error404';

const ArticlePage = ({ match }) => {
  document.documentElement.classList.remove("nav-open");
  const name = match.params.name;
  const article = articleContent.find(article => article.name === name);
  
  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
  
  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
  
      const body = await result.json();
      
      setArticleInfo(body);
    }
    fetchData(); 
  }, [name]);
  
  if (!article) return <Error404 />
  
  const otherArticles = articleContent.filter(article => article.name !== name);
  
  return (
    <>
      <Navbar />
      <Header />
      
      <div className="main">
        <div className="section text-center">
          <Container>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <h1>{article.title}</h1>

                {article.content.map((paragraph, key) => (
                  <p key={key}>{paragraph}</p>
                 ))}

                <h3>Other Articles</h3>
                <ArticlesList articles={otherArticles} colWidth={2} />
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      
      <Footer />
    </>
  );
}

export default ArticlePage;