import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import ScrollToTop from 'components/ScrollToTop.js';

// styles
import "assets/css/bootstrap.min.css";
import "assets/scss/paper-kit.scss?v=1.2.0";
import "assets/demo/demo.css?v=1.2.0";

// pages
import HomePage from "views/HomePage.js";
import ArticlePage from "views/Article.js";
import AllArticles from "views/AllArticles.js";
import About from "views/About.js";
import Contact from "views/Contact.js";
import Error404 from "views/Error404.js";

import LandingPage from "views/LandingPage.js";
import ProfilePage from "views/ProfilePage.js";
import RegisterPage from "views/RegisterPage.js";

ReactDOM.render(
  <BrowserRouter>
    <ScrollToTop />
    <Switch>
      <Route path="/" component={ HomePage } exact/>
      <Route path="/about" component={ About } />
      <Route path="/blog" component={ AllArticles } exact />
      <Route path="/a/:name" component={ ArticlePage } exact />
      <Route path="/contact" component={ Contact } exact />
      <Route path="/examples/LandingPage" component={ LandingPage } exact />
      <Route path="/examples/ProfilePage" component={ ProfilePage } exact />
      <Route path="/examples/RegisterPage" component={ RegisterPage } exact />
      <Route component={Error404} />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
