import React from 'react';
import {
  Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button,   Container,
  Row,
  Col,
} from 'reactstrap';
import { Link } from 'react-router-dom';
import Header from "assets/img/header.jpg";

function ArticlesList({ articles, colWidth }) {
    const rows = [...Array( Math.ceil(articles.length / colWidth) )];
    const articlesRows = rows.map((row, idx) => articles.slice(idx * colWidth, idx * colWidth + colWidth) );

    const content = articlesRows.map((row, idx) => (
        <Row key={idx}>    
          {row.map((article, key) => (
            <Col key={key}>
              <Link className='Button' to={`/a/${article.name}`}>
                <Card>
                  <CardImg top width="100%" src={ Header } alt="Card image cap" />
                  <CardBody>
                    <CardTitle tag="h5">{article.title}</CardTitle>
                    <CardSubtitle tag="h6" className="mb-2 text-muted">Thomas Pickup - 29th Jan 2020</CardSubtitle>
                    <CardText>{article.content[0].substring(0,150)}...</CardText>
                  </CardBody>
                </Card>
              </Link>
            </Col>
          ))}
        </Row> )
    );
    return (
      <div className="section text-center">
        <Container>
          {content}
        </Container>
      </div>
    );
}

export default ArticlesList;